.card {
  margin-top: 150px;
  margin-bottom: 50px;
  width: 45vw;
  height: auto;
  /* height: 450px; */
  /* background-image: linear-gradient(163deg, #00ff75 0%, #3700ff 100%); */
  border-radius: 20px;
  transition: all 0.3s;
  /* margin: 20px; */
  cursor: pointer;
  transition: all 0.3s ease-in;
  color: white;
}
.card:hover {
  transform: translateY(-20px);
}

.card2 {
  /* width: 300px;
  height: 450px; */
  padding: 36px 16px 16px 16px;
  width: 45vw;
  height: auto;
  background-color: #1a1a1a60;
  border-radius: 20px;
  border: 0.1px solid #ffffff41;
  transition: all 0.2s;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 10px;
  padding-top: 20px;
}

.card2:hover , .card2:active{
  transform: scale(0.98);
  border-radius: 20px;
  background-color: #1a1a1a;
}

.card:hover, .card:active {
  box-shadow: 0px 0px 30px 1px rgba(0, 255, 117, 0.3);
  background-image: linear-gradient(163deg, #00ff75 0%, #3700ff 100%);
}

.heading {
  margin-top:24px ;
  font-size: 3vw;
  font-weight: 600;
}

.userData {
  width: 100%;
  padding: 8px;
  margin: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mandatory {
  color: #ff00e1;
}

.input_container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 85%;
  margin: 8px;
}

.input_label {
  font-weight: 400;
  font-size: 22px;
  line-height: 38px;
  color: #ffffff;
  margin-bottom: 6px;
  padding-left: 12px;
}

.input,
.message {
  /* all: unset; */
  background-color: #00000000;
  border: 1.5px solid #d9d9d9;
  padding: 8px 16px;
  width: 100%;
  height: 7vh;
  color: white;
  font-size: 16px;
  border-radius: 15px;
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}

.message {
  height: 15vh;
}

.button {
  margin-top: 10px;
  margin-bottom: 70px;
}

@media screen and (max-width: 768px) {
  /* .registerForm {
    width: 90vw;
    align-self: center;
    padding: 36px 8px 8px 8px;
    margin-right: 16px;
  } */
  .card,.card2{
    width: 90vw;
    margin-right: 16px;
  }
  .card2{
    padding: 36px 8px 8px 8px;
  }

  .heading {
    font-size: 4vh;
    font-weight: 600;
  }

  .userData {
    padding: 4px;
    margin: 4px;
  }

  .input_container {
    width: 100%;
  }

  .input_label {
    font-size: 2.5vh;
  }

  .input,
  .message {
    font-size: 2vh;
  }

  .background {
    padding: 0px;
  }
}
